
.Container_Acompanante{
    background-image: url(../../../Utils/Img/Formularios/fondo1.png);
    width: 100%;
    height: 69vh;
    margin: none;
    align-items: center;
    justify-content: center;
    display: flex;
}
.Box2{
    background-color: rgb(248, 248, 248);
    width: 40%;
    height: 67vh ;
   align-items: center;
   justify-content: center;
   animation-name: llegadaAbajo;
   animation-duration: 3s;
}
@keyframes llegadaAbajo {
   0%{
       opacity: 0;
       transform:translateY(-100px) ;
   }
   60%{
       opacity: 0.9;
       transform: translateY(40px)
   }
   80%{
       opacity: 1;
       transform: translateY(25px);
   }
   100%{
       opacity: 1;
       transform: translateY(0);
   }
}
#enviar1, #borrar{
    background-color: #E86433;
    color: white;
    border-radius: 15%;
    width: 20%;
    height: 35px;
    margin-left: 3%;
    border: none;
    margin-top: 20px;
}
h1{
    padding-top: 4%;
}
p, h1{
    display: flex;
   align-items: center;
   justify-content: center;
