	text-align: center;
	line-height: 45px;
	background-color: #ffffff;
	color: #000000;
	text-decoration: none;
 }
 
 
 nav ul li:hover {
	display: inline-block;
	max-width: 190px;
	width: 100%;
	text-align: center;
	line-height: 45px;
	background-color: #EF7F3F;
